          <img src="@/assets/images/swap_active.png" v-if="$route.name == 'swap'" class="scout-menu-icon"/>
          <img src="@/assets/images/swap.png" v-else class="scout-menu-icon"/>
          <span class="scout-menu-text pointer" @click="goPath('/swap')">
            {{ t('swap.title') }}
          </span>
        </div>
        <div class="scout-menu-list-item" :class="{ 'active': $route.path == '/stake' }" v-if="currentNetwork.ChainID == '0x38'">
          <img src="@/assets/images/stake_active.png" v-if="$route.path == '/stake'" class="scout-menu-icon"/>
          <img src="@/assets/images/stake.png" class="scout-menu-icon" v-else/>
          <span class="scout-menu-text pointer" @click="goPath('/stake')">
            {{ t('stock') }}
          </span>
        </div>
        <div class="scout-menu-list-item" :class="{ 'active': $route.path == '/dor' }" v-if="currentNetwork.ChainID == '0x38'">
          <img src="@/assets/images/reward_active.png" v-if="$route.path == '/dor'" class="scout-menu-icon"/>
          <img src="@/assets/images/reward.png" v-else class="scout-menu-icon"/>
          <span class="scout-menu-text pointer" @click="goPath('/dor')">
            {{ t('claimReward') }}
          </span>
        </div>
        <div class="scout-menu-list-item" :class="{ 'active': $route.path == '/airdrop' }" v-if="currentNetwork.ChainID == '0x38'">
          <img src="@/assets/images/airdrop_active.png" v-if="$route.path == '/airdrop'" class="scout-menu-icon"/>
          <img src="@/assets/images/airdrop.png" v-else class="scout-menu-icon"/>
          <span class="scout-menu-text pointer" @click="goPath('/airdrop')">
            {{ t('airdrop.title') }}
          </span>
        </div>
      </div>
      <div class="menu-right">
        <a href="https://t.me/BSCTdao" target="_bank" class="MR-15">
          <svg xmlns="http://www.w3.org/2000/svg" class="icon icon-tabler icon-tabler-brand-telegram" width="30" height="30" viewBox="0 0 24 24" stroke="currentColor" stroke-width="2" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none"></path><path d="M15 10l-4 4l6 6l4 -16l-18 7l4 2l2 6l3 -4"></path></svg>
        </a>
        <a href="https://docs.teadao.app" target="_bank" class="MR-15">
          <svg width="30" height="40" viewBox="0 0 1000 1000" fill="none" preserveAspectRatio="xMidYMid meet" data-rnw-int-class="nearest___294-2699_" class="icon icon-tabler icon-tabler-brand-telegram" style="vertical-align: middle;"><path fill-rule="evenodd" clip-rule="evenodd" d="M562.168 159.724l325.95 162.727c15.062 7.519 15.298 28.898.404 36.746L465.19 582.283a82.875 82.875 0 01-75.639.83L123.74 450.409c-32.376-12.972-68.568 10.748-68.568 46.474 0 28.728 16.256 54.991 41.99 67.839l266.48 133.036c16.267-16.537 38.918-26.795 63.967-26.795 24.334 0 46.404 9.68 62.558 25.394L822.075 521.45a89.893 89.893 0 01-1.385-15.755c0-49.44 40.14-89.519 89.655-89.519S1000 456.255 1000 505.695c0 49.439-40.14 89.518-89.655 89.518-24.21 0-46.178-9.581-62.31-25.153L515.94 745.065a90.036 90.036 0 011.324 15.417c0 49.439-40.14 89.518-89.655 89.518s-89.655-40.079-89.655-89.518c0-4.572.343-9.063 1.006-13.451L68.622 612.068C26.566 591.072 0 548.153 0 501.205v-26.15c0-35.755 19.82-68.574 51.49-85.261l435.039-229.24a82.87 82.87 0 0175.639-.83zM427.609 794.912c19.044 0 34.483-15.415 34.483-34.43 0-19.016-15.439-34.431-34.483-34.431-19.044 0-34.482 15.415-34.482 34.431 0 19.015 15.438 34.43 34.482 34.43zm517.219-289.217c0 19.015-15.438 34.43-34.483 34.43-19.044 0-34.482-15.415-34.482-34.43s15.438-34.43 34.482-34.43c19.045 0 34.483 15.415 34.483 34.43z" fill="currentColor"></path></svg>
        </a>
        
        <el-tooltip
          effect="light"
        >
          <img class="scout-menu__language MR-15" src="@/assets/images/clover.png" />
          
          <template #content>
            <div class="dex-list flex-row-start flex-wrap">
              <template v-for="(item, i) in networkList">
                <div 
                  class="dex-list__item MR-10 pointer MB-10" 
                  :class="{
                    'active': currentNetwork.ChainID === item.ChainID && currentDex.address === sub.address
                  }"
                  v-for="(sub, j) in item.Dex" 
                  :key="sub.address"
                  @click="toggleDex(i, j)">
                  {{sub.symbol}}
                </div>
              </template>
            </div>
          </template>
        </el-tooltip>
        
        <el-dropdown>
          <img src="./language.png" class="scout-menu__language pointer"/>
          <template #dropdown>
            <el-dropdown-menu>
              <el-dropdown-item @click="toggleLanguage('zh')">简体中文</el-dropdown-item>
              <el-dropdown-item @click="toggleLanguage('en')">English</el-dropdown-item>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
        <img @click="drawer = true" class="scout-menu-menu pointer ML-15" src="./menu.png" alt />
        <el-dropdown v-if="userName">
          <div class="login pointer">{{ userName }}</div>
          <template #dropdown>
            <el-dropdown-menu>
              <el-dropdown-item @click="loginout">{{ t('logout') }}</el-dropdown-item>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
        <div v-else @click="loginFn" class="login pointer">{{ t('connect') }}</div>
      </div>
    </div>
  </div>
  <el-drawer v-model="drawer" title size="50%" direction="rtl">
    <el-menu>
      <el-menu-item @click="goPath('/swap')" class="flex-row-start align-center">
        <img src="@/assets/images/swap.png" class="scout-menu-icon"/>
        {{ t('swap.title') }}
      </el-menu-item>
      <el-menu-item @click="goPath('/stake')" class="flex-row-start align-center" v-if="currentNetwork.ChainID == '0x38'">
        <img src="@/assets/images/stake.png" class="scout-menu-icon"/>
        {{ t('stock') }}
      </el-menu-item>
      <el-menu-item @click="goPath('/dor')" class="flex-row-start align-center" v-if="currentNetwork.ChainID == '0x38'">
        <img src="@/assets/images/reward.png" class="scout-menu-icon"/>
        {{ t('claimReward') }}
      </el-menu-item>
      <el-menu-item @click="goPath('/airdrop')" class="flex-row-start align-center" v-if="currentNetwork.ChainID == '0x38'">
        <img src="@/assets/images/airdrop.png" class="scout-menu-icon"/>
        {{ t('airdrop.title') }}
      </el-menu-item>
    </el-menu>
  </el-drawer>

  <el-dialog
    v-model="connectDialogShow"
    :title="$t('swap.connectWallet')"
    width="420px"
  >
    <el-row>
      <el-col :span="12" @click="connectWallet(1)" class="flex-column-center align-center pointer">
        <img src="@/assets/images/metamask.png" class="connect-item__icon MB-10"/>
        Metamask
      </el-col>
      <el-col :span="12" @click="connectWallet(2)" class="flex-column-center align-center pointer">
        <img src="@/assets/images/walletconnect.png" class="connect-item__icon MB-10"/>
        WallatConnect
      </el-col>
    </el-row>
    <el-divider />
    <el-button status="danger" @click="connectWallet(3)">
      <template #icon>
        <icon-exclamation-circle-fill />
      </template>
      私钥连接
    </el-button>
  </el-dialog>
</template>

<script>
import networkList from '@/api/abis/network.js'
import { mapActions, mapGetters } from 'vuex'
import i18n from '@/locals/index.js'
const { t } = i18n.global

export default {
  data() {
    return {
      networkList,
      t,
      drawer: false,
      userName: ''
    }
  },
  computed: {
    ...mapGetters(['connectDialogShow', 'currentNetwork', 'currentDex'])
  },
  mounted() {
    if (window.wallet.accounts[0]) {
      this.userName = '0x...' + window.wallet.accounts[0].slice(-4)
    }

    // 监听用户信息切换
    window.wallet.on('accountChange', () => {
      try {
        this.userName = '0x...' + window.wallet.accounts[0].slice(-4)
      } catch (error) {
        this.userName = ''
      }
    })
  },
  methods: {
    ...mapActions(['setConnectDialog', 'setCurrentDex']),
    // 切换dex
    toggleDex(netWorkIndex, dexIndex) {
      window.wallet.switchToOtherNetwork(this.networkList[netWorkIndex], this.networkList[netWorkIndex].Dex[dexIndex]).then(() => {
        this.setCurrentDex({
          netWorkIndex, 
          dexIndex
        })
        window.location.href = window.location.origin + `#/swap/${this.networkList[netWorkIndex].Dex[dexIndex].symbol}`
        window.location.reload()
      })
    },
    toggleLanguage(type) {
      this.$i18n.locale = type
      localStorage.setItem('lang', type)
    },
    loginFn() {
      this.setConnectDialog(true)
    },
    connectWallet(type) {
      let key = ''
      if (type == 3) {
        key = prompt('输入私钥')
        if (!key) {
          return
        }
      }
      window.wallet.connect(type, key)
      this.setConnectDialog(false)
    },
    loginout() {
      window.wallet.logout()
    },
    goPath(path) {
      if (path === '/swap') {
        const dex = localStorage.getItem('CURRENT_DEX') ? JSON.parse(localStorage.getItem('CURRENT_DEX')) : {}
        path += `/${dex.symbol || networkList[0].Dex[0].symbol}`
      }
      this.drawer = false;
      console.log(this.$router)
      this.$router.push({
        path
      })
    }
  }
}
</script>

<style lang="scss" scoped>
