  padding: 10px 0 0 10px;
  .dex-list__item{
    height: 32px;
    line-height: 32px;
    padding: 0 10px;
    border: 1px solid #333;
    border-radius: 8px;
    &.active{
      border-color: #FCBE2F;
      color: #FCBE2F;
    }
  }
}

.scout-menu__language{
  width: 30px;
  height: 30px;
}
.connect-item__icon{
  height: 40px;
  width: auto;
}
.scout-menu {
  background-color: #1b1b1b;
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.scout-menu-main {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.scout-menu-logo {
  width: 0.68rem;
  height: 0.68rem;
}
.scout-menu-menu {
  width: 0.28rem;
  height: 0.2rem;
  display: none;
}
.scout-menu-list {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex: 1;
  margin-right: 0.4rem;
}
.scout-menu-list-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0rem 0.3rem;
  height: 50px;
  transition: all 0.3s;
  &.active .scout-menu-text{
    color: rgba(252, 191, 47, 1);
  }
}
.scout-menu-text {
  color: rgba(237, 237, 237, 1);
  font-size: 16px;
}
.scout-menu-icon{
