  height: 18px;
  margin-right: 8px;
}
.el-drawer__close-btn {
  padding: 0rem;
}
.menu-right {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.menu-right .login {
  margin-left: 0.3rem;
  padding: 0.1rem 0.3rem;
  background-color: #FCBE2F;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.16rem;
  color: #fff;
  border-radius: 0.06rem;
}
@media only screen and (max-width: 992px) {
  .scout-menu-list {
    display: none;
  }
  .scout-menu-menu{
		display: block !important;
	}
}
@media only screen and (max-width: 768px) {
	
}
</style>
<style>
