  background-color: transparent !important;
  border: 0 !important;
}
.el-menu-item {
  color: #fff !important;
}
.el-menu-item:hover {
  color: #fff !important;
  background-color: rgb(79, 75, 75) !important;
}
.router-link-active {
  color: #fff;
}
